*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

#root {
  min-height: 100vh;
} 

@font-face {
  font-family: "GetSchwiftyRegular";
  src: local("GetSchwiftyRegular"),
    url("./fonts/get_schwifty.ttf") format("truetype");
  font-weight: normal;
}

body * {
  font-family: GetSchwiftyRegular, "Brush Script MT", cursive;
  letter-spacing: 0.2em;
}

h2, p, button, li {
   font-family: GetSchwiftyRegular, "Brush Script MT", cursive !important;
}

span {
  font-family: GetSchwiftyRegular, "Brush Script MT", cursive !important;
  font-weight: bold;
  color: #f7ed73;
}
